        </ol>
	<button class="submit-button" type="button" @click="saveResponse">Save</button>
	<button class="submit-button" type="button" @click="jailResponse">Jail</button>
	<div class="jail-message">{{ jailMessage }}</div>
	<div class="save-message">{{ saveMessage }}</div>
	</div>
    </div>
    <div class="input-section" v-else-if="buttonType === 'haiku' || buttonType === 'haikudone'">
    <form @submit.prevent="submitForm">
    <!-- rest of your form -->
    <div v-if="buttonType === 'haiku'">
          <h2>Haiku Craft</h2>
          <p> {{haiku_first_line}}</p>		
          <label>Second line of the haiku:</label>
          <input type="text" v-model="haiku_second_line" placeholder="Enter your haiku line" required />
          <button class="submit-button" type="button" @click="submitHaikuLine">Submit</button>
          <p> {{haiku_third_line}}</p>
        </div>
		<div v-else-if="buttonType === 'haikudone'">
          <h2>Haiku Craft</h2>
          <p class="italic-text"> {{haiku_first_line}}</p>		
          <p class="italic-text"> {{haiku_second_line}}</p>
          <p class="italic-text"> {{haiku_third_line}}</p>
	<button class="submit-button" type="button" @click="saveResponse">Save</button>
	<button class="submit-button" type="button" @click="jailResponse">Jail</button>
	<div class="jail-message">{{ jailMessage }}</div>
		<div class="save-message">{{ saveMessage }}</div>
        </div>
        <!-- rest of your form -->
      </form>
    </div>
	
    </div>
	<button class="long-submit-button" type="button" @click="downloadJson">Download Report </button>
	<button class="long-submit-button" type="button" @click="goBack">Back</button>
    </div>

    </div>


</template>
