<script>
import axios from "axios";
export default {
  data() {
    return {
      group_name: "",
      response: null,
      isLoading: false,
      buttonType: null,
      alchemy_word: "",
      alchemy_response: null,
      haiku_first_line: "",
      haiku_second_line: "",
      haiku_third_line: "",
      savedResponses: {},
      saveMessage: "",
      file: '',
      fileUploaded: false,
      save_path: "",
      sleeperStarted:false,
      search_model :"tfidf",
      gen_model: "lm",
      num_keywords:2,
      num_docs:5,
      newSessionForm: false,
      status: "", 	  
    };
  },
  methods: {
  

handleFileUpload() {
  if(this.$refs.file) {
    this.file = this.$refs.file.files[0];
  } else {
    console.log('Error: this.$refs.file is undefined');
  }
},

  async endSession() {
  try {
    let formData = new FormData();
    const res = await axios.post("https://nodenogginflask.azurewebsites.net/end", formData);
    this.response = res.data;
  } catch (error) {
    console.error(error);
  }
  
    try {
    let formData = new FormData();
    const res = await axios.post("https://nodenogginflask.azurewebsites.net/status", formData);
    this.status = res.sleeper_status;
  } catch (error) {
    console.error(error);
  }
  
  this.fileUploaded = false;
  this.newSessionForm = false;
  
  },
  
  async goBack() {
  this.fileUploaded = false;
  this.newSessionForm = false;
  },
  
  async startNewSession() {
  this.newSessionForm = true;
  try {
    let formData = new FormData();
    const res = await axios.post("https://nodenogginflask.azurewebsites.net/status", formData);
    this.status = res.data.sleeper_status;
  } catch (error) {
    console.error(error);
  }
  },

  async newSession() {
    this.isLoading = true;

    // create a new FormData instance
    let formData = new FormData();

    // append the 'file' field and the actual file to formData
    formData.append('file', this.file);
    formData.append('save_path',this.save_path);
    formData.append('num_keywords', this.num_keywords);
    formData.append('num_docs',this.num_docs);
    formData.append('search_model', this.search_model);
    formData.append('gen_model',this.gen_model);

    try {
      // replace 'url' with your actual upload URL
      const res = await axios.post("https://nodenogginflask.azurewebsites.net/upload", formData);

      if (res.status === 200) {
        this.fileUploaded = true;
	
      }
    } catch (error) {
      console.error(error);
    }

    this.isLoading = false;
	
	try {
    let formData = new FormData();
    const res = await axios.post("https://nodenogginflask.azurewebsites.net/status", formData);
    this.status = res.sleeper_status;
  } catch (error) {
    console.error(error);
  }
	
  },
  async joinSession() {
        this.fileUploaded = true;
  },

  async submitMemories() {
    this.buttonType = 'memories';
    await this.submitForm("https://nodenogginflask.azurewebsites.net/memories");
  },
  async submitDreams() {
    this.buttonType = 'dreams';
    await this.submitForm("https://nodenogginflask.azurewebsites.net/dreams");
  },
  async submitTopics() {
    this.buttonType = 'topics';
    await this.submitForm("https://nodenogginflask.azurewebsites.net/topics");
  },

    async submitAlchemy() {
      this.buttonType = "alchemy";
      this.alchemy_response = null; // clear previous response
      this.response = "alchemy";
    },
    async submitAlchemyWord() {
      const url = "https://nodenogginflask.azurewebsites.net/alchemy"; // replace with the correct endpoint
      const params = {
        group_name: this.group_name,
        alchemy_word: this.alchemy_word, // send the entered word as a parameter
      };
      this.isLoading = true;
      try {
        const res = await axios.post(url, params);
        this.alchemy_response = res.data; // display the response
      } catch (error) {
        console.error(error);
      }
      this.isLoading = false;
    },
	async downloadJson() {
      const dataStr = "data:text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(this.savedResponses));
      const downloadAnchorNode = document.createElement('a');
      downloadAnchorNode.setAttribute("href", dataStr);
      downloadAnchorNode.setAttribute("download", "sleeper_responses.json");
      document.body.appendChild(downloadAnchorNode); // required for firefox
      downloadAnchorNode.click();
      downloadAnchorNode.remove();
    },
  async submitHaiku() {
    this.buttonType = 'haiku';
    const url = "https://nodenogginflask.azurewebsites.net/haiku";
    const params = {
      group_name: this.group_name,
    };
    this.isLoading = true;
    try {
      const res = await axios.post(url, params);
      this.haiku_first_line = res.data;
    } catch (error) {
      console.error(error);
    }
    this.isLoading = false;
  },
  async submitHaikuLine() {
    this.buttonType = 'haikudone';
    const url = "https://nodenogginflask.azurewebsites.net/haikucomplete";
    const params = {
      group_name: this.group_name,
      first_line : this.haiku_first_line,
      second_line: this.haiku_second_line, // include the user's haiku second line
    };
    this.isLoading = true;
    try {
      const res = await axios.post(url, params);
      this.haiku_third_line = res.data;
    } catch (error) {
      console.error(error);
    }
    this.isLoading = false;
  },
 async saveResponse() {
    const responseToSave = this.buttonType === 'alchemy' ? this.alchemy_response :
                           this.buttonType === 'haiku' || this.buttonType === 'haikudone' ?
                           {
                             haiku_first_line: this.haiku_first_line,
                             haiku_second_line: this.haiku_second_line,
                             haiku_third_line: this.haiku_third_line,
                           } :
                           this.response;
    if (!this.savedResponses["saved"]) {
      this.savedResponses["saved"]={};}

    if (this.savedResponses["saved"][this.buttonType]) {
      this.savedResponses["saved"][this.buttonType].push(responseToSave);
    } else {
      this.savedResponses["saved"][this.buttonType] = [responseToSave];
    }

    this.saveMessage = "Saved!";
    setTimeout(() => { this.saveMessage = ""; }, 1000);  // clear the message after 1 seconds
},
 async jailResponse() {
    const responseToSave = this.buttonType === 'alchemy' ? this.alchemy_response :
                           this.buttonType === 'haiku' || this.buttonType === 'haikudone' ?
                           {
                             haiku_first_line: this.haiku_first_line,
                             haiku_second_line: this.haiku_second_line,
                             haiku_third_line: this.haiku_third_line,
                           } :
                            this.response;

    if (!this.savedResponses["jail"]) {
      this.savedResponses["jail"]={};}
	if (this.savedResponses["jail"][this.buttonType]) {
      this.savedResponses["jail"][this.buutonType].push(responseToSave);
    } else {
      this.savedResponses["jail"][this.buttonType] = [responseToSave];
    }

    this.saveMessage = "Jailed!";
    setTimeout(() => { this.saveMessage = ""; }, 1000);  // clear the message after 1 seconds
},

 
async submitForm(url) {
  const params = {
    group_name: this.group_name,
  };
  this.isLoading = true;
  this.response = null;  // add this line to clear the previous response
  try {
    const res = await axios.post(url, params);
    this.response = res.data;
  } catch (error) {
    console.error(error);
  }
  this.isLoading = false;
},

},
}
</script>
